<template>
    <div class="screen-design"
         v-loading="loading"
         element-loading-background="rgba(0, 0, 0, 0.7)">
        <Menu :config="config" :state="state"></Menu>
        <Paint :config="config" :state="state"></Paint>
        <Layer :config="config" :state="state"></Layer>
        <Config :config="config" :state="state"></Config>
        <ContextMenu :config="config" :state="state"></ContextMenu>
        <Publisher :config="config" :state="state.publisher"></Publisher>
        <DataSourceEdit v-if="state.addDataSourceModal.visible" :config="state.addDataSourceModal"></DataSourceEdit>
        <DynamicComponentList :config="config" :state="state.dynamicComponent"></DynamicComponentList>
        <DynamicComponentEdit :config="config" :state="state.dynamicComponent"></DynamicComponentEdit>
        <Loader :config="config" :state="state.loader"></Loader>
        <PictureStore :config="config" :state="state.pictureStore"></PictureStore>
        <div class="action">
            <el-tooltip class="item" effect="dark" content="自定义组件" placement="right">
                <el-button type="text" @click="showDynamicComponentModal">
                    <i class="icon iconfont icon-star"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="模板" placement="right">
                <el-button type="text" @click="template">
                    <i class="icon iconfont icon-layer"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="发布" placement="right">
                <el-button type="text" @click="publish">
                    <i class="icon iconfont icon-plane"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="保存" placement="right">
                <el-button type="text" @click="save">
                    <i class="icon iconfont icon-save"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="预览" placement="right">
                <el-button type="text" @click="preview">
                    <i class="icon iconfont icon-view"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="导入" placement="right">
                <el-button type="text" @click="loader">
                    <i class="icon iconfont icon-upload"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="导出" placement="right">
                <el-button type="text" @click="exportConfig">
                    <i class="icon iconfont icon-download"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="帮助" placement="right">
                <el-button type="text" @click="help">
                    <i class="icon iconfont icon-help"></i>
                </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="退出" placement="right">
                <el-button type="text" @click="exit">
                    <i class="icon iconfont icon-exit"></i>
                </el-button>
            </el-tooltip>
        </div>
        <TemplateList v-if="state.templateList.visible" :templateList="state.templateList" :config="config">
        </TemplateList>
    </div>
</template>

<script>
    import '../assets/styles/design/main.scss'
    import API from '../config/api'
    import CONST from '../config/const'
    import _ from 'lodash'

    const Menu = () => import('@/components/screen/Menu')
    const Layer = () => import('@/components/screen/Layer')
    const Config = () => import('@/components/screen/Config')
    const Paint = () => import('@/components/screen/Paint')
    const ContextMenu = () => import('@/components/screen/ContextMenu')
    const Publisher = () => import('@/components/screen/Publisher')
    const Loader = () => import('@/components/screen/Loader')
    const PictureStore = () => import('@/components/screen/PictureStore')
    const DynamicComponentList = () => import('@/components/screen/DynamicComponentList')
    const DynamicComponentEdit = () => import('@/components/screen/DynamicComponentEdit')
    const DataSourceEdit = () => import('@/components/datasource/DataSourceEdit')
    const TemplateList = () => import('@/components/template/TemplateList')

    export default {
        name: 'Design',
        data() {
            return {
                // 加载效果
                loading: true,
                // 储存配置页的状态
                state: null,
                // 页面配置文件
                config: null,
            }
        },
        created() {
            this.initScreen()
            this.initActionSequence()
            // 点击任意位置取消选中组件
            window.addEventListener('click', this.cancelSelectComponent)
        },
        mounted() {
            this.loadDynamicComponent()
        },
        methods: {
            /**
             * 根据url参数加载配置
             */
            initScreen() {
                this.state = {
                    // 当前选中的组件
                    select: {},
                    // 当前选中的组件索引
                    index: null,
                    // 组件菜单
                    menu: {
                        dragMode: false,
                        component: null
                    },
                    // 发布弹窗
                    publisher: {
                        visible: false
                    },
                    // 图库弹窗
                    pictureStore: {
                        visible: false,
                        item: null,
                        prop: null
                    },
                    // 自定义组件管理
                    dynamicComponent: {
                        //是否展示
                        visible: false,
                        // 组件列表
                        list: [],
                        // 编辑模式
                        editMode: {
                            visible: false,
                            item: null
                        },
                    },
                    // 显示新增数据源窗口
                    addDataSourceModal: {
                        dataSource: null,
                        visible: false
                    },
                    // 右键点击选中的组件索引
                    contextMenu: {
                        show: false,
                        // 点击弹出菜单的组件id
                        refId: null,
                        ids: null,
                        style: {top: 0, left: 0}
                    },
                    // 配置加载弹窗
                    loader: {visible: false},
                    drag: {
                        id: null,
                        isDragging: false,
                        start: null,
                        end: null,
                    },
                    // 栅格状态
                    grid: {
                        id: 'grid-' + new Date().getTime(),
                        // 是否处于编辑模式下，即栅格系统是否选中某一行
                        configMode: false,
                        // 当前选中，用来编辑的行的属性
                        row: null,
                        // 当前激活的列，用于拖动组件时的摆放
                        dragMode: {
                            mouse: {x: null, y: null},
                            column: null
                        }
                    },
                    // 取色器
                    colorPicker: {
                        enable: false,
                        list: []
                    },
                    // undo、redo队列
                    action: {
                        sequence: [],
                        index: null
                    },
                    //模板配置
                    templateList: {
                        //是否展示
                        visible: false,
                        //模板表单数据
                        dataSource: {},
                        //模板表示：是否是自定义模板
                        isCustomTemplate: true,
                        //是否是新增
                        isNew: true
                    },
                }
                this.config = {
                    // 页面id
                    id: null,
                    // 页面名称
                    name: '未命名',
                    // 封面
                    cover: null,
                    // 状态 0-未发布 / 1-已发布
                    state: 0,
                    // 大屏配置
                    base: {
                        // 启用栅格
                        enableGrid: false,
                        size: {width: 1800, height: 1200},
                        backgroundColor: 'rgba(0,0,0,0)',
                        backgroundImage: null,
                        scaleMode: '0',
                        grid: {
                            style: {height: 100},
                            columns: [{
                                id: "col-" + new Date().getTime() + '1',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }, {
                                id: "col-" + new Date().getTime() + '2',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }, {
                                id: "col-" + new Date().getTime() + '3',
                                style: {xs: 8, sm: 8, md: 8, lg: 8, xl: 8},
                                rows: [],
                                components: {}
                            }]
                        },
                        // 全屏显示
                        fullScreen: {
                            id: null,
                            component: null
                        },
                    },
                    // 组件顺序
                    sequence: [],
                    // 组件
                    components: {}
                }
                let id = this.$route.query.id
                if (!id) {
                    this.loading = false
                    return
                }
                this.ajax({
                    url: API.SCREEN.GET_BY_ID,
                    params: {id: id},
                }).then(response => {
                    let config = response.data
                    if (!config) {
                        this.$message.error('错误：未找到可视化。')
                    }
                    if (typeof config.base == "string") {
                        config.base = JSON.parse(config.base)
                    }
                    if (typeof config.sequence == "string") {
                        config.sequence = JSON.parse(config.sequence)
                    }
                    if (typeof config.components == "string") {
                        config.components = JSON.parse(config.components)
                    }
                    if ("ON" === config.state) {
                        config.state = 1;
                    } else {
                        config.state = 0;
                    }
                    this.config = config
                }).finally(() => {
                    this.loading = false
                })
            },
            /**
             * 初始化undo、redo队列
             */
            initActionSequence() {
                this.state.action.sequence.push(_.cloneDeep(this.config))
                this.state.action.index = 0
            },
            /**
             * 取消选中组件
             * @param e
             */
            cancelSelectComponent(e) {
                // 取消组件编辑
                this.$set(this.state, "select", {})
                // 取消栅格编辑模式
                this.state.grid.configMode = false
                this.state.grid.row = null
            },
            /**
             * 保存
             */
            save() {
                let config = this.formatConfigParam(this.config)
                config.type = this.$route.query.saveType;
                this.ajax({
                    url: API.SCREEN.SAVE,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    if (response.success) {
                        if (!this.config.id) {
                            this.config.id = response.data.id;
                        }
                        this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                        this.$router.replace({
                            query: {
                                id: this.config.id
                            }
                        })
                    }
                })
            },
            /**
             * 发布
             */
            publish() {
                this.state.publisher.visible = true
            },
            /**
             * 将可视化配置转为参数
             */
            formatConfigParam(config) {
                let param = _.cloneDeep(config)
                for (let key in param) {
                    let value = param[key]
                    if (_.isObject(value)) {
                        param[key] = JSON.stringify(value)
                    }
                }
                return param
            },
            /**
             * 预览
             */
            preview() {
                let config = this.formatConfigParam(this.config)
                this.ajax({
                    url: API.SCREEN.PREVIEW,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    let link = this.$router.resolve({
                        path: './presentation',
                        query: {id: response.data.id}
                    })
                    window.open(link.href, '_blank')
                })
            },
            /**
             * 加载配置
             */
            loader() {
                this.state.loader.visible = true
            },
            /**
             * 导出
             */
            exportConfig() {
                let config = _.cloneDeep(this.config)
                config.id = null
                let aTag = document.createElement('a')
                let blob = new Blob([JSON.stringify(config, null, 4)])
                aTag.download = "config-" + new Date().getTime() + ".txt"
                aTag.href = URL.createObjectURL(blob)
                aTag.click()
                URL.revokeObjectURL(blob)
            },
            /**
             * 打开帮助页
             */
            help() {
                window.open("http://www.tyrival.com/books/vision-guide/", "_blank")
            },
            /**
             * 退出编辑页面
             */
            exit() {
                this.$confirm('退出编辑界面会丢失所有未保存的操作, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$router.push('./overview')
                }).catch(() => {

                })
            },
            /**
             * 模板
             */
            template() {
                let dataSource = this.formatConfigParam(this.config)
                this.state.templateList.visible = true;
                this.state.templateList.dataSource = dataSource;
            },
            /**
             * 自定义组件
             */
            loadDynamicComponent() {
                this.ajax({
                    url: API.DYNAMIC_COMPONENT.LIST,
                    method: CONST.AJAX.GET,
                }).then(response => {
                    if (response.data.success) {
                        this.state.dynamicComponent.list = response.data.data
                    }
                })
                // TODO 正式版需修改
                // this.ajax({
                //     url: API.DYNAMIC_COMPONENT.LIST,
                //     method: CONST.AJAX.POST,
                //     data: {},
                // }).then(response => {
                //     if (response.success) {
                //         this.state.dynamicComponent.list = response.data
                //     }
                // })
            },
            /**
             * 自定义组件
             */
            showDynamicComponentModal() {
                this.state.dynamicComponent.visible = true;
            }
        },
        components: {
            'Menu': Menu,
            'Layer': Layer,
            'Config': Config,
            'Paint': Paint,
            'Publisher': Publisher,
            'ContextMenu': ContextMenu,
            'DataSourceEdit': DataSourceEdit,
            'TemplateList': TemplateList,
            'Loader': Loader,
            'PictureStore': PictureStore,
            'DynamicComponentList': DynamicComponentList,
            'DynamicComponentEdit': DynamicComponentEdit
        }
    }
</script>

<style scoped>

</style>
